<template>
  <div class="mgt-20">
    <table class="my-table">
      <thead>
      <tr>
        <th v-for="item in thead">{{item}}</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="item,index in list">
        <td>{{pageData.pageSize*(curIndex-1)+index+1}}</td>
        <td>{{item.email}}</td>
        <td>{{item.senderEmail}}</td>
        <td>{{item.receiverEmail}}</td>
        <td>{{item.emailTitle}}</td>
        <td>{{item.sensitiveWords}}</td>
        <td>{{item.sendTime}}</td>
      </tr>
      </tbody>
    </table>
    <div class="mgt-20">
      <pagination :curIndex.sync="curIndex" :pageData="pageData" @changePage="changePage"></pagination>
    </div>
  </div>
</template>
<script>
  import {trackDetail as api} from '../../../api/trackDetail'
  import pagination from 'components/pagination';
  export default{
    name: 'emailDetail',
    components: {pagination},
    data(){
      return {
        curIndex: 1,
        pageData: {
          curPage: 1,
          pageSize: 10,
          total: 30,  // 必须传
        },
        thead: ['序号', '邮箱网址', '发件人', '收件人', '邮件标题', '邮件内容中敏感词', '发送时间'],
        list: []
      }
    },
    methods: {
      changePage(index){
        this.curIndex = index
        this.getList()
      },
      getList(){
        api.getEmailList({id: this.$route.params.id,pageIndex:this.curIndex,pageSize:this.pageData.pageSize}).then((rst) => {
          if(rst.code === 1){
          this.list = rst.data.list
          this.pageData.curPage = this.curIndex
          this.pageData.total = rst.data.total
        }
      })
      }
    },
    created(){
      this.getList()
    }
  }
</script>
<style lang="scss" scoped>
  @import "~assets/css/_myTable.scss";
</style>



