<template>
  <div class="mgt-20">
    <table class="my-table">
      <thead>
      <tr>
        <th>
          <el-checkbox></el-checkbox>
        </th>
        <th v-for="item in thead">{{item}}</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="item,index in list">
        <td>
          <el-checkbox></el-checkbox>
        </td>
        <td>{{pageData.pageSize*(curIndex-1)+index+1}}</td>
        <td>{{item.netName}}</td>
        <td>{{item.netAddress}}</td>
        <td>{{item.recordNum}}</td>
        <td>{{item.num}}</td>
        <td>{{item.startTime}}</td>
        <td>{{item.endTime}}</td>
      </tr>
      </tbody>
    </table>
    <div class="mgt-20">
      <pagination :curIndex.sync="curIndex" :pageData="pageData" @changePage="changePage"></pagination>
    </div>
  </div>
</template>
<script>
  import {trackDetail as api} from '../../../api/trackDetail'
  import pagination from 'components/pagination';
  export default{
    name: 'visitUyghurNet',
    components: {pagination},
    data(){
      return {
        curIndex: 1,
        pageData: {
          curPage: 1,
          pageSize: 10,
          total: 0,  // 必须传
        },
        thead: ['序号', '网站名称', '网址', '网站备案号', '访问次数', '最早访问时间', '最后离开时间'],
        list: []
      }
    },
    methods: {
      changePage(index){
        this.curIndex = index
        this.getList()
      },
      getList(){
        api.getVisitUyghurList({id: this.$route.params.id,pageIndex:this.curIndex,pageSize:this.pageData.pageSize}).then((rst) => {
          if(rst.code === 1){
          this.list = rst.data.list
          this.pageData.curPage = this.curIndex
          this.pageData.total = rst.data.total
        }
      })
      }
    },
    created(){
      this.getList()
    }
  }
</script>
<style lang="scss" scoped>
  @import "~assets/css/_myTable.scss";
</style>




