<template>
  <div class="mgt-20">
    <table class="my-table">
      <thead>
      <tr>
        <th>
          <el-checkbox></el-checkbox>
        </th>
        <th v-for="item in thead">{{item}}</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="item,index in list">
        <td>
          <el-checkbox></el-checkbox>
        </td>
        <td>{{pageData.pageSize*(curIndex-1)+index+1}}</td>
        <td>{{item.areaName}}</td>
        <td>{{item.bsId}}</td>
        <td>{{item.mobileDeviceId}}</td>
        <td>{{item.appearNum}}</td>
        <td>{{item.appearTime}}</td>
      </tr>
      </tbody>
    </table>
    <div class="mgt-20">
      <pagination :curIndex.sync="curIndex" :pageData="pageData" @changePage="changePage"></pagination>
    </div>
    <div class="map-box">
      <img class="suspicious-map" src="../../../assets/img/suspicious_map.png" alt="">
    </div>
  </div>
</template>
<script>
  import pagination from 'components/pagination';
  import {trackDetail as api} from '../../../api/trackDetail'
  export default{
    name: 'keyArea',
    components: {pagination},
    data(){
      return {
        curIndex: 1,
        pageData: {
          curPage: 1,
          pageSize: 10,
          total: 0,  // 必须传
        },
        thead: ['序号', '重点区域名称', '基站编号', '手机设备号', '出现次数', '出现时间'],
        list: []
      }
    },
    methods: {
      changePage(index){
        this.curIndex = index
        this.getList()
      },
      getList(){
        api.getKeyAreaList({id: this.$route.params.id,pageIndex:this.curIndex,pageSize:this.pageData.pageSize}).then((rst) => {
          if(rst.code === 1){
          this.list = rst.data.list
          this.pageData.curPage = this.curIndex
          this.pageData.total = rst.data.total
        }
      })
      }
    },
    created(){
      this.getList()
    }
  }
</script>
<style lang="scss" scoped>
  @import "~assets/css/_myTable.scss";
</style>



