import * as http from "./api";
const uri = 'https://mock.eolinker.com/8RiESwDd5a5506d56f9453f947d0ff45a1b5e487a9b6182?uri='
export const trackDetail = {
  /**
   * 可疑人员查询--活动轨迹--获取个人信息
   * @param params
   * @returns {*}
   */
  getPersonInfo(params){
    return http.post({baseUri: uri, url: '/suspiciousperson/search/getPersonInfo'}, params)
  },
  /**
   * 可疑人员查询--活动轨迹--重点区域详情
   * @param params
   * @returns {*}
   */
  getKeyAreaList(params){
    return http.post({baseUri: uri, url: '/suspiciousperson/search/getKeyAreaList'}, params)
  },
  /**
   * 可疑人员查询--活动轨迹--网吧详情
   * @param params
   * @returns {*}
   */
  getInternetCafes(params){
    return http.post({baseUri: uri, url: '/suspiciousperson/search/getInternetCafes'}, params)
  },
  /**
   * 可疑人员查询--活动轨迹--宾馆详情
   * @param params
   * @returns {*}
   */
  getHotelList(params){
    return http.post({baseUri: uri, url: '/suspiciousperson/search/getHotelList'}, params)
  },
  /**
   * 可疑人员查询--活动轨迹--访问维文网站
   * @param params
   * @returns {*}
   */
  getVisitUyghurList(params){
    return http.post({baseUri: uri, url: '/suspiciousperson/search/getVisitUyghurList'}, params)
  },
  /**
   * 可疑人员查询--活动轨迹--访问涉恐网站
   * @param params
   * @returns {*}
   */
  getVisitTerrorismList(params){
    return http.post({baseUri: uri, url: '/suspiciousperson/search/getVisitTerrorismList'}, params)
  },
  /**
   * 可疑人员查询--活动轨迹--邮箱详情
   * @param params
   * @returns {*}
   */
  getEmailList(params){
    return http.post({baseUri: uri, url: '/suspiciousperson/search/getEmailList'}, params)
  },
  /**
   * 可疑人员查询--活动轨迹--使用APP详情
   * @param params
   * @returns {*}
   */
  getAppUsageList(params){
    return http.post({baseUri: uri, url: '/suspiciousperson/search/getAppUsageList'}, params)
  }
}



