<template>
  <div class="mgt-20">
    <table class="my-table">
      <thead>
      <tr>
        <th v-for="item in thead">{{item}}</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="item,index in list">
        <td>{{pageData.pageSize*(curIndex-1)+index+1}}</td>
        <td>{{item.mobileDeviceId}}</td>
        <td>{{item.operation}}</td>
        <td>{{item.url}}</td>
        <td>{{item.appName}}</td>
        <td>{{item.appUsage}}</td>
        <td>{{item.operateTime}}</td>
      </tr>
      </tbody>
    </table>
    <div class="mgt-20">
      <pagination :curIndex.sync="curIndex" :pageData="pageData" @changePage="changePage"></pagination>
    </div>
  </div>
</template>
<script>
  import {trackDetail as api} from '../../../api/trackDetail'
  import pagination from 'components/pagination';
  export default{
    name: 'useApp',
    components: {pagination},
    data(){
      return {
        curIndex: 1,
        pageData: {
          curPage: 1,
          pageSize: 10,
          total: 30,  // 必须传
        },
        thead: ['序号', '手机设备号', '操作行为', '提供app的网址', 'app名称', 'app用途', '操作时间'],
        list: []
      }
    },
    methods: {
      changePage(index){
        this.curIndex = index
        this.getList()
      },
      getList(){
        api.getAppUsageList({id: this.$route.params.id,pageIndex:this.curIndex,pageSize:this.pageData.pageSize}).then((rst) => {
          if(rst.code === 1){
          this.list = rst.data.list
          this.pageData.curPage = this.curIndex
          this.pageData.total = rst.data.total
        }
      })
      }
    },
    created(){
      this.getList()
    }
  }
</script>
<style lang="scss" scoped>
  @import "~assets/css/_myTable.scss";
</style>



