<template>
  <div class="track-index">
    <div class="person-info">
      <router-link class="back-btn" tag="span" to="/suspiciousperson/personCheck"><i class="iconfont icon-fanhui"></i> 返回</router-link>

      <div class="person-detail">
        <div>
          <img src="../../../assets/img/portrait.png"/>
        </div>
        <div class="mgl-15">
          <p><span>姓名:</span>{{info.name}}</p>
          <p class="mgt-19"><span>民族:</span>{{info.nation}}</p>
          <p class="mgt-19"><span>身份证号:</span>{{info.idNumber}}</p>
          <p class="mgt-19"><span>手机号:</span>{{info.mobile}}</p>
        </div>
        <div class="mgl-60">
          <p><span>户籍所在地:</span>{{info.domicile}}</p>
          <p class="mgt-19"><span>手机号归属地:</span>{{info.phoneBelong}}</p>
          <p class="mgt-19"><span>手机设备号:</span>{{info.mobileDeviceId}}</p>
        </div>
      </div>
    </div>
    <ul class="third-nav">
      <router-link v-for="menu in menus" tag="li" :to='{name:menu.name,query:query,params:params}'>{{menu.title}}
      </router-link>
    </ul>
    <router-view></router-view>
  </div>
</template>
<script>
  import {trackDetail as api} from '../../../api/trackDetail'
  export default{
    name: "trackIndex",
    data(){
      return {
        params: this.$route.params,
        query: this.$route.query,
        menus: [
          {title: '重点区域详情', name: 'keyArea'},
          {title: '网吧详情', name: 'internetCafes'},
          {title: '宾馆详情', name: 'hotelDetail'},
          {title: '访问维文网站', name: 'visitUyghurNet'},
          {title: '访问涉恐网站', name: 'visitTerrorismNet'},
          {title: '邮件详情', name: 'emailDetail'},
          {title: '使用APP详情', name: 'useApp'}
        ],
        info: {}
      }
    },
    created(){
      api.getPersonInfo({id: this.$route.params.id}).then((rst) => {
        if(rst.code === 1){
        this.info = rst.data
      }
    })
    }
  }
</script>
<style lang="scss" scoped>
  .third-nav {
    height: 30px;
    background-color: rgba(0, 69, 190, 0.6);
    border-radius: 1px;
    line-height: 30px;
    padding: 0 0 0 110px;

  li {
    width: 130px;
    cursor: pointer;
    position: relative;
    display: inline-block;
    color: #939fc2;
    text-align: center;

  &:hover,  &.active {
    background-color: #2367e2;
    border-radius: 1px;
    color: #ffffff;
  }

  }

  }

  .mgl-15 {
    margin-left: 15px;
  }

  .mgl-60 {
    margin-left: 60px;
  }

  .mgt-19 {
    margin-top: 19px;
  }

  .person-info {
    margin: 21px 0 60px 0;
  }

  .person-detail {
    display: flex;
    justify-content: center;

  span {
    color: #c2cae2;
    margin-right: 8px;
  }

  }
  .back-btn {
    height: 14px;
    font-family: MicrosoftYaHei;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    line-height: 40px;
    letter-spacing: 0px;
    color: #939fc2;
    cursor: pointer;
    .iconfont{
      font-size: 14px;
      color:#939fc2;
    }
  }
</style>



