<template>
  <div class="mgt-20">
    <table class="my-table">
      <thead>
      <tr>
        <th v-for="item in thead">{{item}}</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="item,index in list">
        <td>{{pageData.pageSize*(curIndex-1)+index+1}}</td>
        <td>{{item.cafeName}}</td>
        <td>{{item.address}}</td>
        <td>{{item.cafeContact}}</td>
        <td>{{item.mobile}}</td>
        <td>{{item.onlineTime}}</td>
        <td>{{item.offlineTime}}</td>
      </tr>
      </tbody>
    </table>
    <div class="mgt-20">
      <pagination :curIndex.sync="curIndex" :pageData="pageData" @changePage="changePage"></pagination>
    </div>
    <div class="map-box">
      <img class="suspicious-map" src="../../../assets/img/suspicious_map.png" alt="">
    </div>
  </div>
</template>
<script>
  import {trackDetail as api} from '../../../api/trackDetail'
  import pagination from 'components/pagination';
  export default{
    name: 'internetCafes',
    components: {pagination},
    data(){
      return {
        curIndex: 1,
        pageData: {
          curPage: 1,
          pageSize: 10,
          total: 0,  // 必须传
        },
        thead: ['序号', '网吧名称', '地址', '网吧联系人', '联系电话', '上线时间', '下线时间'],
        list: []
      }
    },
    methods: {
      changePage(index){
        this.curIndex = index
        this.getList()
      },
      getList(){
        api.getInternetCafes({id: this.$route.params.id,pageIndex:this.curIndex,pageSize:this.pageData.pageSize}).then((rst) => {
          if(rst.code === 1){
          this.list = rst.data.list
          this.pageData.curPage = this.curIndex
          this.pageData.total = rst.data.total
        }
      })
      }
    },
    created(){
      this.getList()
    }
  }
</script>
<style lang="scss" scoped>
  @import "~assets/css/_myTable.scss";
</style>



